@import url('https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap');

.player {
  height:20vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.time-control {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90%;
  padding: 0 0.25rem;
  font-family: 'Architects Daughter', cursive;
  font-weight: 900;
  input {
    width: 100%;
    cursor: pointer;
    -webkit-appearance: none;
    background: transparent;
  }
}

input[type="range"]:focus {
  outline: none;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 1rem;
  height: 1rem;
}

input[type="range"]::-moz-range-thumb {
  -moz-appearance: none;
  background:transparent;
  border: none;
  width: 1rem;
  height: 1rem;
}

.play-control {
  width: 90%;
  display: flex;
  justify-content: space-between;
  i {
    font-size: 3rem;
    cursor: pointer;
  }
}

.track {
  width: 100%;
  height: 1rem;
  margin: 0 1rem;
  position: relative;
  overflow: hidden;
  border-radius: 8px;
}

.animate-track {
  background-color: rgb(40, 69, 83);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
}

input {
  -webkit-appearance: none;
  background: transparent;
}
