.nav {
  height: 10vh;
  padding: 0.5rem 1rem 0 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  button {
    background-color: white;
    font-family: 'Lobster', cursive;
    font-size: 1rem;
    transition: all 0.5s linear;
    z-index: 10;
    border: 0.25rem solid rgb(97, 56, 56);
    border-radius: 5px;
    height: 3rem;
    i {
      padding-left: 0.5rem;
    }
    &:hover{
      background-color: rgb(206, 163, 22);
      color:white;
    }
  }
  h1 {
    z-index: 10;
    color:red
  }
}

